<template>
    <div class="mythology-container">
        <form>
            <div class="input-container">
                <input type="checkbox" id="myth" />
                <label for="myth">Mythology</label>
            </div>
        </form>
        <button>Counter</button>
    </div>
</template>

<style>
  .mythology-container {
    background-image: url(../../../assets/mythology-bg.png);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .mythology-container button {
    outline: none;
    border: none;
    box-shadow: none;
    background: none;
  }

  .mythology-container label,
  .mythology-container button {
    color: #fff;
    font-family: "Old London";
    font-size: 50px;
  }

  .mythology-container input {
    padding: 0;
    height: initial;
    width: initial;
    margin-bottom: 0;
    display: none;
    cursor: pointer;
  }

  .mythology-container label {
    position: relative;
    cursor: pointer;
  }

  .mythology-container input:checked + label:after {
    content: '';
    display: block;
    position: absolute;
    top: 21px;
    left: -11px;
    width: 6px;
    height: 14px;
    border: solid #0079bf;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
  }

  .mythology-container label:before {
    content:'';
    -webkit-appearance: none;
    background-color: transparent;
    border: 2px solid #0079bf;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
    padding: 10px;
    display: inline-block;
    position: relative;
    vertical-align: middle;
    cursor: pointer;
    left: -20px;
    top: -6px;
  }

  

</style>

  